import React, { Component } from 'react';
import './style.css';
import { Redirect } from 'react-router-dom';
import { withRouter } from 'react-router-dom';

export default class Header extends Component {
    // constructor(props) {
    //     super(props);
    //     // this.state = {isToggleOn: true};
    
    //     // Эта привязка обязательна для работы `this` в колбэке.
    //     this.handleClick = this.handleClick.bind(this);
    //     this.handleClick = this.handleClick.bind(this);
    //   }

      handleClick(e) {
       console.log(`/${e.target.name}`);
       return <Redirect to={`/${e.target.name}`} />
      }
    
    render(){
    return(
        <div className='header'>
            <h1>Header</h1>
            <img src='img.png' alt='img' className='header__img'></img>
            <button type='button' name='store' onClick={this.handleClick}>STORE</button>
            <button type='button' name='layout' onClick={this.handleClick}>LAYOUT</button>
            <button type='button' name='slider' onClick={this.handleClick}>SLIDER</button>
        </div>
    )}
}

